export const handleShowString = (name: string, length = 30, numberSub = 20) => {
  if (!name) return ""
  if (name.length > length) {
    return name.substring(0, numberSub) + " ..."
  } else {
    return name
  }
}

export const handleShowDate = (date: string, showDateWithSecond = false, showOnlyDate = false) => {
  if (!date) return ""
  const newDate = new Date(date);
  newDate.setHours(newDate.getHours() - 7);
  const day = newDate.getDate();
  const month = newDate.getMonth() + 1;
  const year = newDate.getFullYear();

  const min = newDate.getMinutes();
  const hour = newDate.getHours();
  const second = newDate.getSeconds();

  if (showOnlyDate) {
    return `${day}/${month}/${year}`
  }
  if (!showDateWithSecond) {
    return `${day}/${month}/${year} ${hour}:${min}`
  } else {
    return `${day}/${month}/${year}  ${hour}:${min}:${second}`
  }
}


import { Dimensions, Platform, PixelRatio } from 'react-native';
const { width } = Dimensions.get('window');

const guidelineBaseWidth = 390;
// const guidelineBaseHeight = 680;

const scale = function (size: number) {
  if (Platform.OS === 'ios') {
    if (Platform.isPad) {
      return Math.round((width / guidelineBaseWidth) * size);
    }
    return Math.round(
      PixelRatio.roundToNearestPixel((width / guidelineBaseWidth) * size),
    );
  } else {
    return Math.round(PixelRatio.roundToNearestPixel(size));
  }
};
export { scale };
